  margin: 1.5rem 0;
}

/* Overlay */
.dark-overlay {
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

/* Forms */
.form .form-group {
  margin: 1.2rem 10rem;
}

.form .form-text {
  display: block;
  margin-top: 0.3rem;
  color: #888;
}

.form input[type='text'],
.form input[type='password'],
.form input[type='date'],
.form input[type='number'],


.form select,
.form textarea {
  display: block;
  width: 100%;
  padding: 0.4rem;
  padding-left:0.8rem;
  font-size: 1.2rem;
  border:  1px solid #ccc;
  transform: capitalize;
  border-radius:0.5rem;
}





.form input[type='submit'],
button {
  font: inherit;
}

.form input:hover[type='text'],
.form input:hover[type='password'],
.form input:hover[type='date'],
.form input:hover[type='number'],
.form input:hover[type='select'],
.form select:hover
 {
  color:#056d5a;
  color:var(--dark-color);
  font-weight:bold;
}


.form textarea:hover{ 
  color:#056d5a; 
  color:var(--dark-color);

}



