.table th,
.table td {
  padding: 1rem;
  text-align: left;
}

.table th {
  background: #f4f4f4;
  background: var(--light-color);
}

/* Navbar */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0;
  border-bottom: solid 1px #e6b141;
  border-bottom: solid 1px var(--primary-color);
  /*opacity: 0.9;*/
  color:#f4f4f4;
}

.flexbox { 
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 3rem;
}



.navbar ul {
  display: flex;
}

.navbar a {
 
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar a:hover {
  color: #e6b141;
  color: var(--primary-color);
}

.navbar .welcome span {
  margin-right: 0.6rem;
}

/* Landing Page */
.landing {
  position: relative;

  height: 100vh;
}

.landing-inner {
  color: #fff;
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

/* Profiles Page */
.profile {
  display: grid;
  grid-template-columns: 2fr 2fr 2fr;
  align-items: center;
  grid-gap: 4rem;
  padding: 1rem;
  line-height: 1.8;
  margin-bottom: 1rem;
  border-radius:5px;
  margin:2rem 2rem 0 2rem;
}

/* Profile Page */
.profile-grid {
  display: grid;
  grid-template-areas:
    'top top'
    'about about'
    'exp edu '
    'github github';
  grid-gap: 1rem;
}




.profile-exp{
  grid-area: exp;
  border-radius: 0.2rem;

}

.profile-top {
  grid-area: top;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  grid-gap: 1rem;
  
}


.profile-top-profile {
  grid-area: top;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  grid-gap: 0.5rem;
  padding: 0 3rem;
  
}

.auth {

  grid-gap: 0.5rem;

  
}




.profile-top img {
  width: 250px;
}

.profile-top .icons a {
  color: #056d5a;
  color: var(--dark-color);
  margin: 0 0.3rem;
}

.icons a {
  color: grey;
  margin: 0 0.3rem;
}

.profile-top .icons a:hover {
  color: #056d5a;
  color: var(--dark-color);

}

.profile-about {
  grid-area: about;
  text-align: center;
  border-radius: 0 0 0.5rem 0.5rem ;

}

.profile-about .skills {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  border-radius: 0.2rem;
}

.profile-exp {
  grid-area: exp;
  border-radius: 0.2rem;
}

.profile-edu {
  grid-area: edu;
  border-radius: 0.5rem;
  
}

.profile-exp h2,
.profile-edu h2 {
  margin-bottom: 1rem;
}

.profile-exp > div,
.profile-edu > div {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: #ccc 1px dotted;
}

.profile-exp > div:last-child,
.profile-edu > div:last-child {
  border: 0;
}

.profile-exp p,
.profile-edu p {
  margin: 0.5rem 0;
}

.profile-github {
  grid-area: github;
}

.profile-github .repo {
  display: flex;
}

.profile-github .repo > div:first-child {
  flex: 7 1;
  flex-basis: 70%;
}

.profile-github > div:last-child {
  flex: 3 1;
  flex-basis: 20%;
}

/* Posts Page */
.post-form .post-form-header {
  background: #e6b141;
  background: var(--primary-color);
  padding: 0.5rem;
}

.post {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-gap: 2rem;
  align-items: center;
}

.post > div:first-child {
  text-align: center;
}

.post img {
  width: 100px;
}

.post .comment-count {
  background: #f4f4f4;
  background: var(--light-color);
  color: #e6b141;
  color: var(--primary-color);
  padding: 0.1rem 0.2rem;
  border-radius: 5px;
  font-size: 0.8rem;
}

.post .post-date {
  color: #aaa;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

/* Mobile Styles */
@media (max-width: 700px) {
  .container {
    margin-top: 8rem;
  }

  .flex-smart {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    margin: 2.5rem;
  }
  
  .btn-smart{
    display: inline-block;
    background: #f4f4f4;
    background: var(--light-color);
    color: #333;
 
    font-size: 1rem;
    border-radius: 4px;
    cursor: pointer;
    /* margin-right: 0.5rem; */
    transition: opacity 0.2s ease-in;
    outline: none;
}



  .hide-sm {
    display: none;
  }

  /* Text Styles */
  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead  {
    font-size: 1rem;
  }

  .home-h1 {
    font-size: 1.6rem;
  }

  /* Navbar */
  .navbar {
    display: block;
    text-align: center;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }

  .navbar h1 {
    margin-bottom: 1rem;
  }

  .navbar .welcome {
    display: none;
  }

  /* Profiles Page */
  .profile {
    grid-template-columns: 1fr;
    text-align: center;
    grid-gap: 2rem;
  }

  .profile-edu {
    grid-template-columns: 1fr;
    text-align: center;
}

  .profile ul {
    display: none;
  }

  /* Profile Page */

  .profile-top img,
  .profile img {
    width: 200px;
    margin: auto;
    margin-top:1rem;
  }

  .profile-grid {
    grid-template-areas:
      'top'
      'about'
      'exp'
      'edu'
      'github';
  }

  .profile-about .skills {
    flex-direction: column;
  }

  .dash-buttons a {
    display: block;
    width: 100%;
    margin-bottom: 0.2rem;
  }

  .post {
    grid-template-columns: 1fr;
  }
  .post a,
  .post button {
    padding: 0.3rem 0.4rem;
  }
}


