:root { 
  --overlay-color: #1b4355;
  }
  
  * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  }
  
.showcase {
  position: inherit;
  right:0;
  width: 100%;
  min-height:100vh;
  padding:100px;

  align-items: center;
  background-color:#217575;
  color:#fff;
  z-index: 2;
  transition: 0.5s;
  }
  
.text {
  position:relative;
  z-index:10;
  font-size: 2rem;
}


.overlay {
  position:absolute; 
  top: 0;
  left: 0;
  width:100%;
  height:100%;
  background:#1b4355;
  background:var(--overlay-color);
  mix-blend-mode: overlay;
  }
  
  
  .social {
    position:absolute; 
    bottom: 20px;
    z-index:10; 
    display:flex;
    justify-content:center; 
    align-items: center;
  }
  
  .social li {
    list-style:none;
  }
  
  .social li a {
    display: inline-block;
    -webkit-filter:invert(1);
            filter:invert(1);
    margin-right: 20px;
    transform: scale(0.5);
    transition:0.5s;
  }
  
  .social li a:hover {
    transform: scale(0.5) translateY(-15px);
  }
  
  
  @media(max-width: 780px) {
    .showcase, 
    .showcase header {
      padding:n40px;
    }
    
    .text h2 {
      font-size: 3em;
    }
  
    .text h3 {
      font-size: 2em;
    }
  }

